.staff {
  @apply my-px80;
  overflow-x: hidden;

  @screen lg {
    @apply my-px140;
    overflow: unset;
  }

  &[class*="bg-"]:not(.bg-transparent) {
    @apply text-center py-px80 my-0;

    @screen lg {
      @apply py-px140 my-0;
    }
  }

  &.is-grid {
    overflow: unset;
  }

  &__load-more {
    font-weight: 600;
  }

  .page-section-content-wrap & {
    width: 100vw;
    margin-left: calc(-1 * (100vw - 100%) / 2);

    @screen lg {
      width: auto;
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__container {
    @apply container mx-auto;

    @screen lg {
      overflow: unset;
    }

    .page-section-content-wrap & {
      @screen lg {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  &__heading {
    @apply col-span-full font-semibold text-px32 leading-px40;

    @screen md {
      @apply leading-px42;
    }

    @screen lg {
      @apply text-px40 leading-px46;
    }
  }

  &__description {
    @apply col-span-full text-px18 leading-px24 mt-px14 tracking-em005;

    @screen md {
      @apply mt-px12 mx-auto;
      width: calc(100vw - 35.6770833334%);
    }

    @screen lg {
      @apply col-span-full text-px18 leading-px24 mx-0 w-full;
    }
  }

  &__cards-container {
    @apply mt-px50;
    width: 100vw;
    margin-left: calc(((100vw - 100%) / 2) * -1);
    padding-left: calc((100vw - 100%) / 2);

    @screen lg {
      @apply col-span-full mt-px55 ml-0 pl-0;
      width: auto;
      overflow: unset;
    }
  }

  &__cards-wrapper {
    @apply h-auto;

    @screen lg {
      @apply grid tw-grid-desktop;
      row-gap: 1.75rem;
    }

    .is-grid & {
      @apply mt-px50;
      flex-flow: column wrap;

      @screen md {
        flex-flow: row wrap;
        justify-content: space-between;
      }

      @screen lg {
        @apply mt-px55;
      }
    }

    .is-grid.researchers & {
      @screen md {
        @apply mr-auto ml-auto;
        width: 83.4834834835%;
      }

      @screen lg {
        width: unset;
      }
    }
  }

  &__card {
    width: 82.7586206897%;
    @apply mr-px12;

    @screen md {
      @apply w-px264 mr-px18;
    }

    @screen lg {
      @apply col-span-4 mr-0;
      width: auto;
    }

    @screen xl {
      @apply col-span-3;
    }

    &:last-child {
      @apply mr-px28;

      @screen md {
        @apply mr-px50;
      }

      @screen lg {
        @apply mr-0;
      }
    }

    .is-grid & {
      &[data-staff-part="last"] {
        display: none;
      }
    }

    .founders & {
      @apply w-full mr-auto ml-auto;

      @screen md {
        @apply mr-0 ml-0;
        width: calc((100% - 28px) / 2);
      }

      @screen lg {
        @apply col-span-6;
        width: auto;
      }

      @screen xl {
        @apply col-span-4;
      }

      & + .staff__card {
        @apply mt-px28;

        @screen md {
          @apply mt-0 mb-px28;
        }

        @screen lg {
          @apply mb-0;
        }
      }

      .information__details { display: none; }
    }

    .researchers.is-grid & {
      @apply mr-auto ml-auto;

      @screen md {
        @apply mr-0 ml-0;
        width: calc((100% - 28px) / 2);
      }

      @screen lg {
        width: unset;
      }

      & + .staff__card {
        @apply mt-px28;

        @screen md {
          @apply mt-0 mb-px28;
        }

        @screen lg {
          @apply mb-0;
        }
      }
    }

    .tile-staff {
      border-radius: 10px;
    }

    .aspect-ratio-3-4 {
      background-color: rgba(243, 243, 247, var(--tw-bg-opacity));
      background-image: url("/wp-content/themes/trevor/static/media/staff-placeholder.png");
      background-position: center center;
      background-repeat: no-repeat;

      img {
        object-fit: cover;
        object-position: center center;
      }
    }
  }

  &__card-heading {
    @apply font-semibold text-px80 leading-px90 tracking-em_001;
  }

  &__card-body {
    @apply block mx-auto mt-px12 text-px20 leading-px26 mt-px12;

    @screen md {
      @apply mt-px15 max-w-px248;
    }

    @screen lg {
      @apply max-w-none mt-px16;
      @apply text-px22 leading-px28 tracking-em_001;
    }
  }

  &__cta-wrap {
    @apply mt-px50;

    @screen lg {
      @apply col-span-full text-center;
    }
  }

  &__cta {
    @apply btn;
  }

  .swiper-pagination {
    @apply mt-px50 ml-pxn28;

    @screen lg {
      display: none;
    }
  }

  .swiper-pagination-bullet {
    @apply bg-current;
  }
}
